/* Route transition classes */
export default {

  Page: {
    height: "100vh",
    width: "100%",
    position: "fixed",
    backgroundColor: "black",
    top: "0",  
  },

  "@global" : {

    ".page-enter": {
      opacity: "0",
      transform: "scale(1.1)"
    },

    ".page-enter-active": {
      opacity: "1",
      transform: "scale(1)",
      transition: "opacity 300ms, transform 300ms"
    },
    
    ".page-exit": {
      opacity: "1",
      transform: "scale(1)"
    },
    
    ".page-exit-active": {
      opacity: "0",
      transform: "scale(0.9)",
      transition: "opacity 300ms, transform 300ms"
    }

  }
  
}